<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">公告列表</block>
		</cu-custom>
		<!-- <homecom class="homclass"></homecom> -->
		<view class="mainsub">
			<!-- 			<view class="cu-bar search bg-white">
				<view class="search-form round" style="margin: 0px;">
					<text class="cuIcon-search"></text>
					<input disabled type="text" placeholder="点击进入查询页面" @click="gosearch('adminuserlist')"></input>
				</view>
								<view class="action" style="margin: 0px;">
								<button class="cu-btn bg-green shadow-blur round">搜索</button>
							</view>
			</view> -->

			<view class="cu-bar bg-white solid-bottom"  v-if="userinfo.isadmin == '是'">
				<button class="cu-btn bg-green shadow" @click="adduser"><text class="cuIcon-roundadd"
						style="font-size: 1.2em;margin-right: 5px;"></text>新增公告</button>
			</view>
			<view class="text-xsl padding" v-if="listdata.length == 0">
				<text class=" cuIcon-info text-blue"></text>
				<view class="textclass">没有可查看的数据</view>
			</view>
			<view class="card-main" v-for="(item,i) in listdata" :key="i" style="margin-bottom: 20px;">
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								公告标题：
							</view>
							<view class="card-maintitle02">
								{{item.title}}
							</view>
						</view>
					</view>
					<view class="card-toptitle02"  v-if="userinfo.isadmin == '是'">
						<text style="color: cornflowerblue;font-weight: bold;"
							@click="updata(item.id,item.title, item.content, item.datetime,item.typestr)">修改</text>
					</view>
				</view>
				<view class="card-content">
					<view class="card-maintitle01">
						公告类型：
					</view>
					<view class="card-maintitle02">
						{{item.typestrtext}}
					</view>
				</view>
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								发布时间：
							</view>
							<view class="card-maintitle02">
								{{item.datetime}}
							</view>
						</view>
					</view>
					<view class="card-toptitle02" v-if="userinfo.isadmin == '是'">
						<text style="color: coral;font-weight: bold;" @click="deletefun(item.id)">删除</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import homecom from "@/components/homecom/homecom.vue"
export default {
	created() {
		this.userinfo = JSON.parse(uni.getStorageSync('userinfo')) 
		
	},
	mounted() {
		this.init()
	},
	components: {
		homecom
	},
	data() {
		return {
			listdata: [],
			userinfo:{}
		}
	},
	methods: {
		async init() {
			let queryobj = {}
			let resdata = await new this.Request(this.Urls.m().announcementquery, queryobj).modepost()
			this.listdata = resdata.map((val,i)=>{
				val.typestrtext = val.typestr == 0 ? '违规通告' : '闭关通知'
				return val
			})

		},
		gosearch(val) {
			uni.navigateTo({
				url: `../search/search?type=${val}`
			});
		},
		updata(id, title, content, datetime,typestr) {
			uni.navigateTo({
				url: `../projectdetail/projectdetail?id=${id}&title=${title}&content=${content}&datetime=${datetime}&typestr=${typestr}`
			});
		},
		adduser() {
			uni.navigateTo({
				url: `../projectdetail/projectdetail?id=add`
			});
		},
		deletefun(val) {
			uni.showModal({
				content: '确定要删除吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.asdeletefun(val)
					}
				}
			})
		},
		async asdeletefun(val) {
			let obj = {
				id:val
			}
			let resdata = await new this.Request(this.Urls.m().announcementdelete, obj).modepost()
			if (resdata == 'success') {
				this.alertobj.diagfun('操作成功')
				setTimeout(() => {
					this.init()
				}, 1500)
			}
		}

	}
}
</script>

<style scoped>
.mainsub {
	padding-left: 5%;
	padding-right: 5%;
}

.card-main {
	width: 100%;
	height: 130px;
	background-color: #f1f1f1;
	border-radius: 5px;
	margin-top: 10px;
	padding: 5%;
	position: relative;
	border: 1px #f1f1f1 solid;
	box-shadow: darkgrey 2px 2px 2px 2px;
}

.card-top {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.card-content {
	display: flex;
	justify-content: flex-start;
	align-items: center;
}


.card-maintitle01 {
	font-weight: bold;
}

.card-maintitle01,
.card-maintitle02 {
	margin-top: 10px;
}

.card-toptitle02 {
	margin-top: 10px;
}

.rightdetail {
	display: flex;
	justify-content: center;
	align-items: center;
}

.text-xsl {
	margin-top: 20%;
	text-align: center;
	font-size: 120px;
}

.textclass {
	margin-top: 5px;
	font-weight: 600;
	font-size: 15px;
	color: darkgray;
}
</style>
